<template>
  <div
    style="
      width: 85%;
      margin: 2vh auto;
      background: rgba(255, 255, 255, 0.9);
      padding: 1vh;
    "
    class="bigbox"
  >
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="货柜汇总" name="first">
        <div class="allSelect">
          <div>
            搜索一下：
            <el-input
              v-model="allCheckData"
              placeholder="请输入内容"
              style="width: 11vw"
              @keyup.enter.native="searchAllData()"
            ></el-input>
            <el-button
              type="primary"
              style="margin: 2.7vh 0 0 1vw; width: 6vw"
              @click="searchAllData()"
              >搜 索</el-button
            >
            <el-button type="primary" @click="allPlace()">一键下单</el-button>
            <el-button type="warning" @click="activeName = 'second'"
              >订单集成</el-button
            >
          </div>
        </div>

        <table class="oneTable">
          <thead>
            <tr>
              <th>
                全选<input
                  type="checkbox"
                  class="entireCheck"
                  @change="entireCheck()"
                />
              </th>
              <th>货柜号</th>
              <th>渠道</th>
              <th>站点</th>
              <th>国家</th>
              <th>产品类别</th>
              <th>产品编码</th>
              <th>品名</th>
              <th>sku</th>
              <th>数量</th>
              <th>仓库类别</th>
              <th>仓库名称</th>
              <th>发货时间</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in tableData" :key="index">
              <td>
                <input
                  type="checkbox"
                  :value="item.ord_id"
                  class="allCheck"
                  @change="oneCheck()"
                />
              </td>
              <td @click="tagCheck(index)">
                {{ item.container_num }}
              </td>
              <td @click="tagCheck(index)">
                {{ item.platform }}
              </td>
              <td @click="tagCheck(index)">{{ item.site }}</td>
              <td @click="tagCheck(index)">{{ item.country }}</td>
              <td @click="tagCheck(index)">
                {{ item.product_type }}
              </td>
              <td @click="tagCheck(index)">
                {{ item.product_code }}
              </td>
              <td @click="tagCheck(index)">
                {{ item.product_name }}
              </td>
              <td @click="tagCheck(index)">{{ item.sku }}</td>
              <td @click="tagCheck(index)">{{ item.sku_num }}</td>
              <td @click="tagCheck(index)">
                {{ item.warehouse_type }}
              </td>
              <td @click="tagCheck(index)">
                {{ item.warehouse_name }}
              </td>
              <td @click="tagCheck(index)">
                {{ item.schedule_date }}
              </td>
              <td>
                <el-button type="primary" @click="onePlace(index)" style=""
                  >确认下单</el-button
                >
              </td>
            </tr>
          </tbody>
        </table>

        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page="page"
          layout="prev, pager, next, jumper"
          :total="total"
          class="pagetotle"
          style="margin: 1vh 0 0 28vw"
        >
        </el-pagination>
      </el-tab-pane>

      <el-tab-pane label="订单集成" name="second">
        <el-form :inline="true">
          <el-form-item label="货柜号">
            <el-input
              size="mini"
              v-model="container_name"
              placeholder="请输入货柜号"
            ></el-input
          ></el-form-item>
          <el-form-item>
            <el-button size="mini" type="primary" @click="filterContainer"
              >搜索</el-button
            >
            <el-button size="mini" type="primary" @click="findAll"
              >重置</el-button
            >
            <el-button
              size="mini"
              type="primary"
              @click="goRoute('/ContainersOrdered')"
            >
              查看已下单货柜
            </el-button>
          </el-form-item>
        </el-form>
        <table style="margin: auto" class="twoTable">
          <thead>
            <tr>
              <th>
                全选
                <input
                  type="checkbox"
                  @change="entireCheckLI()"
                  class="entireCheckLI"
                />
              </th>
              <th>货柜号</th>
              <th>站点</th>
              <th>国家</th>
              <th>产品编码</th>
              <th>产品名称</th>
              <th>类别</th>
              <th>集成数量</th>
              <th>仓库类别</th>
              <th>仓库名称</th>
              <th>sku</th>
              <th>发货日期</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in tableData2" :key="index">
              <td>
                <input
                  type="checkbox"
                  class="allCheckLI"
                  :value="item.ord_id"
                  @change="oneCheckLI()"
                />
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.container_num }}
              </td>
              <td @click="tagCheckLI(index)">{{ item.site }}</td>
              <td @click="tagCheckLI(index)">
                {{ item.country }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.product_code }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.product_name }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.product_type }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.sku_num }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.warehouse_type }}
              </td>
              <td @click="tagCheckLI(index)">
                {{ item.warehouse_name }}
              </td>
              <td>{{ item.sku }}</td>
              <td @click="tagCheckLI(index)">
                {{ item.schedule_date }}
              </td>
              <td>
                <el-button type="primary" @click="oneSubmitPlace(index)"
                  >确认下单</el-button
                >
              </td>
            </tr>
          </tbody>
        </table>
        <el-button
          type="primary"
          @click="allSubmitPlace()"
          style="margin: 2vh 0 2vh 38vw; width: 10vw"
          >一 键 下 单</el-button
        >
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      AllTableData2: [], // 表格2所有数据
      container_name: "", // 请输入货柜号
      activeName: "first",

      allCheckData: "", //模糊搜索

      tableData: [], //表格数据
      tableData2: [], //下单界面表格数据

      total: 0, //页码当前页
      page: 1 //
    };
  },

  methods: {
    goRoute(val) {
      this.$router.push({
        path: val
      });
    },
    findAll() {
      this.container_name = "";
      let data = JSON.stringify(this.AllTableData2);
      this.tableData2 = JSON.parse(data);
    },
    filterContainer() {
      console.log("搜索数据", this.tableData2, this.AllTableData2);
      this.tableData2 = this.AllTableData2.filter(i => {
        return i.container_num == this.container_name;
        // return i.container_num.includes(this.container_name);
      });
    },
    searchAllData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/stock_management/order_confirmation/",
        method: "get",
        params: {
          page: 1,
          condition: this.allCheckData
        }
      }).then(res => {
        console.log(res);

        this.tableData = res.data.data;

        this.countryList = res.data.re_list[0];
        this.typeList = res.data.re_list[1];
        this.platformList = res.data.re_list[2];
        this.siteList = res.data.re_list[3];
        this.total = res.data.count_data[0].count_count_data / 5;
        this.page = 1;
        this.tableData2 = [];
      });
    },
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/stock_management/order_confirmation/",
        method: "get",
        params: {
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data;
        this.countryList = res.data.re_list[0];
        this.typeList = res.data.re_list[1];
        this.platformList = res.data.re_list[2];
        this.siteList = res.data.re_list[3];
        this.total = res.data.count_data[0].count_count_data / 5;
        // 测试
        // this.tableData = [
        //   { container_num: "D120", platform: "Amazon", product_type: "2B" },
        //   { container_num: "D122", platform: "Amazon", product_type: "2B" },
        //   { container_num: "D124", platform: "Amazon", product_type: "2B" }
        // ];
      });
    },
    onePlace(index) {
      //单个下单
      // let w = 0; //W变量用于判断集成界面是否有产品编码和货柜号相同的 相同的只需要数量相加成一条数据
      // if (this.tableData2.length == 0) {
      //   this.tableData2.push(this.tableData[index]);
      // } else {
      //   for (let x = 0; x < this.tableData2.length; x++) {
      //     if (
      //       this.tableData[index].container_num ==
      //         this.tableData2[x].container_num &&
      //       this.tableData[index].product_code ==
      //         this.tableData2[x].product_code
      //     ) {
      //       this.tableData2[x].sku_num =
      //         parseInt(this.tableData[index].sku_num) +
      //         parseInt(this.tableData2[x].sku_num);
      //       w++;
      //     }
      //   }
      //   if (w == 0) {

      //   }
      // }
      this.tableData2.push(this.tableData[index]);
      this.tableData.splice(index, 1);

      let allCheck = document.querySelectorAll(".allCheck");
      for (let i = 0; i < allCheck.length; i++) {
        allCheck[i].checked = false;
      }

      console.log(this.tableData2, "打印表格数据");
      let result = JSON.stringify(this.tableData2);
      this.AllTableData2 = JSON.parse(result);
    },
    allPlace() {
      //全部移送购物车
      let allCheck = document.querySelectorAll(".allCheck");
      let demo = [];
      // let demo1 = [];
      for (let i = 0; i < allCheck.length; i++) {
        if (allCheck[i].checked == true) {
          // for (let j = 0; j < this.tableData.length; j++) {
          // if (this.tableData[j].ord_id == allCheck[i].value) {
          // let w = 0; //W变量用于判断集成界面是否有产品编码和货柜号相同的 相同的只需要数量相加成一条数据
          // if (this.tableData2.length == 0) {
          this.tableData2.push(this.tableData[i]);
          // } else {
          // for (let x = 0; x < this.tableData2.length; x++) {
          // if (
          //   this.tableData[j].container_num ==
          //     this.tableData2[x].container_num &&
          //   this.tableData[j].product_code ==
          //     this.tableData2[x].product_code
          // ) {
          //   this.tableData2[x].sku_num =
          //     parseInt(this.tableData[j].sku_num) +
          //     parseInt(this.tableData2[x].sku_num);
          //   //  console.log(x+1)
          //   w++;
          // }
          // }
          // if (w == 0) {
          //   this.tableData2.push(this.tableData[j]);
          // }
          // }
          demo.push(i);
          // }
          // }
        }
      }
      for (let i = 0; i < demo.length; i++) {
        this.tableData.splice(demo[demo.length - i - 1], 1);
      }
      for (let i = 0; i < allCheck.length; i++) {
        allCheck[i].checked = false;
      }
      console.log(this.tableData2, "打印表格数据");
      let result = JSON.stringify(this.tableData2);
      this.AllTableData2 = JSON.parse(result);
    },
    oneSubmitPlace(index) {
      //一个确认下单
      let allCheckLI = document.querySelectorAll(".allCheckLI");
      let formData = new FormData();
      let demo = [];
      let obj = {
        container_code: this.tableData2[index].container_num,
        product_number: this.tableData2[index].product_code,
        product_name: this.tableData2[index].product_name,
        product_type: this.tableData2[index].product_type,
        plan_order: this.tableData2[index].sku_num,
        transport: this.tableData2[index].warehouse_type,
        warehouse_name: this.tableData2[index].warehouse_name,
        dates: this.tableData2[index].schedule_date,
        warehouse_id: this.tableData2[index].warehouse_id,
        country: this.tableData2[index].country,
        store: this.tableData2[index].site,
        port_name: this.tableData2[index].wharf,
        sku: this.tableData2[index].sku,
        unique_id: this.tableData2[index].unique_id,
        id: this.tableData2[index].ord_id
      };
      demo.push(obj);
      demo = JSON.stringify(demo);
      formData.append("params", demo);
      this.$axios
        .post(
          this._url2 + "server/stock_management/order_confirmation/",
          formData
        )
        .then(res => {
          if (res.data.code == 200) {
            alert("下单成功");
            this.tableData2.splice(index, 1);
            for (let i = 0; i < allCheckLI.length; i++) {
              allCheckLI[i].checked = false;
            }
          }
        });
    },
    allSubmitPlace() {
      //全部确认下单
      let allCheckLI = document.querySelectorAll(".allCheckLI");
      let demo1 = [];
      let demo = [];
      for (let i = 0; i < allCheckLI.length; i++) {
        if (allCheckLI[i].checked == true) {
          for (let j = 0; j < this.tableData2.length; j++) {
            if (this.tableData2[j].ord_id == allCheckLI[i].value) {
              let obj1 = {
                container_code: this.tableData2[j].container_num,
                product_number: this.tableData2[j].product_code,
                product_name: this.tableData2[j].product_name,
                product_type: this.tableData2[j].product_type,
                plan_order: this.tableData2[j].sku_num,
                transport: this.tableData2[j].warehouse_type,
                warehouse_name: this.tableData2[j].warehouse_name,
                dates: this.tableData2[j].schedule_date,
                warehouse_id: this.tableData2[j].warehouse_id,
                country: this.tableData2[j].country,
                store: this.tableData2[j].site,
                port_name: this.tableData2[j].wharf,
                sku: this.tableData2[j].sku,
                unique_id: this.tableData2[j].unique_id,
                id: this.tableData2[j].ord_id
              };
              demo1.push(obj1);
              demo.push(j);
            }
          }
        }
      }
      for (let i = 0; i < demo.length; i++) {
        //删除被下单的东西
        this.tableData2.splice(demo[demo.length - i - 1], 1);
      }

      let formData = new FormData();
      demo1 = JSON.stringify(demo1);
      console.log(demo1);
      formData.append("params", demo1);
      this.$axios
        .post(
          this._url2 + "server/stock_management/order_confirmation/",
          formData
        )
        .then(res => {
          if (res.data.code == 200) {
            alert("下单成功");
            for (let i = 0; i < allCheckLI.length; i++) {
              allCheckLI[i].checked = false;
            }
          }
        });
    },
    handleClick(tab, event) {
      console.log(tab, event);
      // console.log();
    },
    entireCheck() {
      //全选框判断(货柜汇总)
      let entireCheck = document.querySelector(".entireCheck");
      let allCheck = document.querySelectorAll(".allCheck");
      if (entireCheck.checked == true) {
        for (let i = 0; i < allCheck.length; i++) {
          allCheck[i].checked = true;
        }
      } else {
        for (let i = 0; i < allCheck.length; i++) {
          allCheck[i].checked = false;
        }
      }
    },
    oneCheck() {
      //单个选择框判断（货柜汇总）
      let allCheck = document.querySelectorAll(".allCheck");
      let entireCheck = document.querySelector(".entireCheck");
      let flag = 0;
      for (let i = 0; i < allCheck.length; i++) {
        if (allCheck[i].checked == false) {
          flag++;
        }
      }
      if (flag === 0) {
        entireCheck.checked = true;
      } else {
        entireCheck.checked = false;
      }
    },
    entireCheckLI() {
      //全选框判断(订单集成)
      let entireCheckLI = document.querySelector(".entireCheckLI");
      let allCheckLI = document.querySelectorAll(".allCheckLI");
      if (entireCheckLI.checked == true) {
        for (let i = 0; i < allCheckLI.length; i++) {
          allCheckLI[i].checked = true;
        }
      } else {
        for (let i = 0; i < allCheckLI.length; i++) {
          allCheckLI[i].checked = false;
        }
      }
    },
    oneCheckLI() {
      //单个选择框判断（订单集成）
      let allCheckLI = document.querySelectorAll(".allCheckLI");
      let entireCheckLI = document.querySelector(".entireCheckLI");
      let flag = 0;
      for (let i = 0; i < allCheckLI.length; i++) {
        if (allCheckLI[i].checked == false) {
          flag++;
        }
      }
      if (flag === 0) {
        entireCheckLI.checked = true;
      } else {
        entireCheckLI.checked = false;
      }
    },

    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      axios({
        url: this._url2 + "server/stock_management/order_confirmation/",
        method: "get",
        params: {
          page: this.page,
          condition: this.allCheckData
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.tableData = res.data.data;
          this.total = res.data.count_data[0].count_count_data / 5;
          let allCheck = document.querySelectorAll(".allCheck");
          for (let i = 0; i < allCheck.length; i++) {
            allCheck[i].checked = false;
          }
          this.tableData2 = [];
        }
      });
    },
    tagCheck(index) {
      //点击表格触发checkbox
      let allCheck = document.querySelectorAll(".allCheck");
      allCheck[index].checked = !allCheck[index].checked;
    },
    tagCheckLI(index) {
      //点击表格触发checkbox
      let allCheckLI = document.querySelectorAll(".allCheckLI");
      allCheckLI[index].checked = !allCheckLI[index].checked;
    }
  },
  created() {
    //   axios({
    //     url: this._url2 + "server/stock_management/is_open/",
    //     method: "get",
    //   }).then((res) => {
    //     if (res.data.code == 200) {
    //       this.getDefaultData();
    //     }else {
    //         alert("你没有权限进入这个页面");
    //         this.$router.push("/Login");
    //     }
    //   });
    this.getDefaultData();
  }
};
</script>

<style scoped>
.allSelect {
  display: flex;
  margin-top: 0vh;
}

.allSelect div {
  flex: 1;
}

table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 65vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

.bigbox >>> .el-tabs__item {
  font-size: 1.5rem;
}

.oneTable tr th:nth-child(1),
.oneTable tr td:nth-child(1),
.oneTable tr th:nth-child(2),
.oneTable tr td:nth-child(2),
.oneTable tr th:nth-child(3),
.oneTable tr td:nth-child(3),
.oneTable tr th:nth-child(4),
.oneTable tr td:nth-child(4),
.oneTable tr th:nth-child(5),
.oneTable tr td:nth-child(5),
.oneTable tr th:nth-child(6),
.oneTable tr td:nth-child(6),
.oneTable tr th:nth-child(10),
.oneTable tr td:nth-child(10),
.oneTable tr th:nth-child(11),
.oneTable tr td:nth-child(11) {
  width: 4.1vw;
}

.oneTable tr th:nth-child(7),
.oneTable tr td:nth-child(7),
.oneTable tr th:nth-child(8),
.oneTable tr td:nth-child(8),
.oneTable tr th:nth-child(9),
.oneTable tr td:nth-child(9) {
  width: 11vw;
}

.twoTable tr th:nth-child(1),
.twoTable tr td:nth-child(1),
.twoTable tr th:nth-child(2),
.twoTable tr td:nth-child(2),
.twoTable tr th:nth-child(3),
.twoTable tr td:nth-child(3),
.twoTable tr th:nth-child(4),
.twoTable tr td:nth-child(4),
.twoTable tr th:nth-child(7),
.twoTable tr td:nth-child(7),
.twoTable tr th:nth-child(8),
.twoTable tr td:nth-child(8),
.twoTable tr th:nth-child(9),
.twoTable tr td:nth-child(9) {
  width: 4.1vw;
}

.twoTable tr th:nth-child(5),
.twoTable tr td:nth-child(5),
.twoTable tr th:nth-child(6),
.twoTable tr td:nth-child(6),
.twoTable tr th:nth-child(11),
.twoTable tr td:nth-child(11) {
  width: 10vw;
}
</style>



